import { StatusCode } from '@wix/error-handler-core/types';
import { statusCodeNumericToText } from './statusCodeNumericToText';

export const statusCodeTextToNumeric: Record<StatusCode, number | undefined> =
  Object.entries(statusCodeNumericToText).reduce((acc, [numeric, text]) => {
    if (text) {
      acc[text] = Number(numeric);
    }
    return acc;
  }, {} as Record<StatusCode, number | undefined>);
